import string from './css.js'

const player ={
    id : undefined,
    n : 1,
    time : 100,
    ui:{
        demo1:document.querySelector('#demo1'),
        demo2:document.querySelector('#demo2')
    },
    init:()=>{//åˆå§‹åŒ–
        player.ui.demo1.innerText = string.substr(0,player.n)
        player.ui.demo2.innerHTML = string.substr(0,player.n)
        player.play()
        player.bindEvents()
    },
    events:{
        '#btnPause':'pause',//åœ¨å£°æ˜Žå‡½æ•°æ—¶ä¸èƒ½ä½¿ç”¨å‡½æ•°å†…çš„å‡½æ•°ï¼Œå…ˆæŠŠpauseå˜æˆå­—ç¬¦ä¸²
        '#btnPlay':'play',
        '#btnSlow':'slow',
        '#btnNormal':'normal',
        '#btnFast':'fast'
    },
    bindEvents:()=>{
        for(let key in player.events){
            if(player.events.hasOwnProperty(key)){
                const value = player.events[key]
                document.querySelector(key).onclick = player[value]//[value]è®©å­—ç¬¦ä¸²valueå˜æˆæ–¹æ³•
            }
        }
    },
    run : ()=>{
        player.n += 1
        if(player.n>string.length){
            window.clearInterval(player.id)
            return
        }
        player.ui.demo1.innerText = string.substr(0,player.n)
        player.ui.demo2.innerHTML = string.substr(0,player.n)
        player.ui.demo1.scrollTop = player.ui.demo1.scrollHeight
    },
    play : () =>{
        player.id = setInterval(player.run,player.time)
    },
    pause : () =>{
        window.clearInterval(player.id)
    },
    slow : () =>{
        player.pause()
        player.time=300;
        player.play()
    },
    normal : ()=>{
        player.pause()
        player.time=100;
        player.play()
    },
    fast: ()=>{
        player.pause()
        player.time=0;
        player.play()
    }
}
player.init()

